const CheckoutForm = () => {
  const stripe = useStripe();
  const elements = useElements();
  const [error, setError] = useState(null);
  const [cardComplete, setCardComplete] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [paymentMethod, setPaymentMethod] = useState(null);
  const [open, setOpen] = React.useState(false);

const handleClickOpen = () => {
  setOpen(true);
};
const handleClose = () => {
  setOpen(false);
};
  const [billingDetails, setBillingDetails] = useState({

  });
  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!stripe || !elements) {
      // Stripe.js has not loaded yet. Make sure to disable
      // form submission until Stripe.js has loaded.
      return;
    }
    if (error) {
      elements.getElement("card").focus();
      return;
    }
    if (cardComplete) {
      setProcessing(true);
    }
    const payload = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
      billing_details: billingDetails,
    });
    setProcessing(false);
    if (payload.error) {
      setError(payload.error);
    } else {
      setPaymentMethod(payload.paymentMethod);
    }
  };
  const reset = () => {
    setError(null);
    setProcessing(false);
    setPaymentMethod(null);

  };
  return paymentMethod ? (
    <div className="Result">
      <div className="ResultTitle" role="alert">
        Payment successful
      </div>
      <div className="ResultMessage">
      Thank you for your order.
      Your order number is #2001539. We have emailed your order confirmation, and will send you an update when your order has shipped.

{paymentMethod.id}
      </div>
      <ResetButton onClick={reset} />
    </div>
  ) : (
    <form className="Form" onSubmit={handleSubmit}>
    <Grid  container
    spacing={0}
    direction="column"
    alignItems="center"
    justify="center"
    
    style={{ minHeight: '20vh' }}>
</Grid>
 <Grid container
    spacing={0}
    direction="column"
    alignItems="left"
    justify="center"
    style={{ minHeight: '1vh' }}>
      <fieldset className="FormGroup">
        <CardField
          onChange={(e) => {
            setError(e.error);
            setCardComplete(e.complete);
          }}
        />
      </fieldset>
      </Grid>
      <Grid container
    spacing={0}
    direction="column"
    alignItems="center"
    justify="center"
    style={{ minHeight: '35vh' }}>
      {error && <ErrorMessage>{error.message}</ErrorMessage>}
      <div >
        <RadioGroup>
      <FormControlLabel  value="card" control={<Radio />} label="Credit card "/>
     
      <FormControlLabel value="delivery"  control={<Radio />} label="Cash on delivery" />
      </RadioGroup>
      </div>
      
      <Button  className="btn" size="large" processing={processing} error={error} disabled={!stripe}  variant="contained"   onClick={handleClickOpen} >
       Done
      </Button> 
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
GoodDay Bakery        </DialogTitle>
        <DialogContent dividers>
         <Typography gutterBottom>   
         Thank you for your order.
      Your order number is #2001539. We have emailed your order confirmation,
       and will send you an update when your order has shipped. <DoneIcon/>
          </Typography>
        </DialogContent>
      </Dialog> 
      
      </Grid>
      
    </form>
    
  );
};
